import React, { useState } from "react"
import { NavLink } from "react-router-dom"
import { useSelector } from "react-redux"
import tw from 'twin.macro'

import UserWidget from "./UserWidget"
import LogoThumb from "../common/LogoThumb"

const Nav = ({ ...props }) => {
  const user = useSelector(store => store.auth.user)
  const [isMenuActive, toggleIsMenuActive] = useState(false)

  const currentYear = new Date().getFullYear()
  return (
    <nav role="navigation" aria-label="main navigation">
      <div>
          <NavLink to="/dashboard">
            <LogoThumb />
          </NavLink>
        {/* Hamburger expandable menu
        <a
          role="button"
          onClick={() => toggleIsMenuActive(!isMenuActive)}
          className={`navbar-burger ${isMenuActive ? "is-active" : ""}`}
          aria-label="menu"
          aria-expanded="false"
        >
          <span aria-hidden="true" />
          <span aria-hidden="true" />
          <span aria-hidden="true" />
        </a>
      */}
      </div>
      <div className={`navbar-menu ${isMenuActive ? "is-active" : ""}`}>
        <div />
        <NavLink to={`/akce`} activeClassName="is-active">
          ve
        </NavLink>
        {Array.from({ length: currentYear - 2013 }, (_, i) => currentYear - i).map((year, i) => (
          <NavLink to={`/akce/${year}`} activeClassName="is-active" key={year}>
            {year}
          </NavLink>
        ))}
        {user && <UserWidget user={user} />}
      </div>
    </nav>
  )
}

export default Nav
